import React, { useContext, useEffect } from 'react';
import { useHistory } from "react-router-dom";
import Button from '../components/button/button';
import './Start.scss';
import useFetch from '../hooks/useFetch';
import { getGameTeam } from '../services/game';
import UserContext from '../context/userContext';

const Start = () => {
    let history = useHistory();
    const user = useContext(UserContext);
    const [ gameTeam, gameTeamLoading, gameTeamError ]
       = useFetch(getGameTeam(user ? user.userId : null), "gameTeams");

    function startGame() {
        history.push('/new');
    }

    useEffect(() => {
        if (gameTeam && !gameTeamError) {
            history.push('/club/' + gameTeam.teamId);
        }
    }, [gameTeam, gameTeamError, history]);

    return !gameTeamLoading && <div className="app-start">
            <div className="heading">
                <div className="text-logo-left">
                    <img src={process.env.PUBLIC_URL + '/LogoFFOText.svg'} className="text-logo" alt="logo" />
                </div>
                <div className="text">Welcome to Fantasy Football Owner {document.cookie['userName']}</div>
                <div className="text-logo-right">
                    <img src={process.env.PUBLIC_URL + '/LogoFFOText.svg'} className="text-logo" alt="logo" />
                </div>
            </div>
            <section className="app-start-action">
                <span>This is a Fantasy Football game based on real events/data</span>
                <span>Manager transitions, player crises, financial ruin?! This is fantasy football like no other.</span>
                <span>Legal notice: this is a non-comercial/amateur game. Players/users do not pay for any service.</span>
                <Button green lg onClick={startGame}>Start new game</Button>
            </section>
        </div>;
}
 
export default Start;