import React, { useState } from 'react';
import PropTypes from 'prop-types';
import BasicRadio from '../../components/radio/radio';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import TeamsInLeague from '../../components/teamsInLeague';
import './TeamSelect.scss';
import CompetitionContext from '../../context/competitionContext';

const TeamSelect = ({competitions, onTeamSelect}) => {
    const [gender, setGender] = useState('men');
    const [selectedCompId, setSelectedCompetition ] = useState(null);

    return competitions.length && (
        <section className="leagues">
            {/* <BasicRadio value={gender} setValue={setGender} /> */}
            <div className={"pad-all-sm marg-bottom-sm flex"} style={{color: "#fff", fontSize: "1.2rem"}}>
                <span className={"text-center"} style={{width: "90%"}}>Take control! Select a team to analyse for financial viability.</span>
                <span className={"align-right"}>Budget: 7m</span>
            </div>

            {gender === 'men' &&
                competitions.filter(c => !c.name.includes('WSL') && 
                    (selectedCompId === null || selectedCompId === c.leagueId)).sort((a, b) => a.tier > b.tier ? 1 : -1).map((comp) => {
                return <Accordion key={comp.leagueId} onChange={(ev, expanded) => {
                    setSelectedCompetition(expanded ? comp.leagueId : null)
                }} className="team-select-acc" TransitionProps={{ unmountOnExit: true }}>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header">
                        {comp.name}
                    </AccordionSummary>
                    <AccordionDetails>
                        <CompetitionContext.Provider value={comp}>
                            <TeamsInLeague onTeamSelect={onTeamSelect} />
                        </CompetitionContext.Provider>
                    </AccordionDetails>
                </Accordion>
            })}
        </section>
    );
}

const wslTeamSelect = ({ competitions, onTeamSelect }) => {
    return competitions.filter(c => c.name.includes('WSL')).sort((a, b) => a.tier > b.tier ? 1 : -1).map((comp) => {
        return <div key={comp.leagueId} className="league">
                <div className="league-name">
                    {comp.name}
                </div>
                <div className="league-logo">
                    <CompetitionContext.Provider value={comp}>
                        <TeamsInLeague onTeamSelect={onTeamSelect} />
                    </CompetitionContext.Provider>
                </div>
            </div>
    })
}

TeamSelect.propTypes = {
    competitions: PropTypes.array,
    onTeamSelect: PropTypes.func
}
 
export default TeamSelect;