import React from 'react';
import PropTypes from 'prop-types';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import AfoTableHead from './AfoTableHead';
import TableRow from '@material-ui/core/TableRow';

const AfoTable = ({className, columns, rows, rowMeta, defaultOrderDescBy, onRowClick}) => {
  const [order, setOrder] = React.useState('asc');
  const [orderBy, setOrderBy] = React.useState(defaultOrderDescBy);
  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };
  return rows ? (<TableContainer className={className} component={Paper}>
      <Table aria-label="simple table">
        {/* <TableHead onRequestSort={handleRequestSort}>
          <TableRow>
              {columns.map((column)=> {
                  return <TableCell key={column.name}>{column.name}</TableCell>;
              })}
          </TableRow>
        </TableHead> */}
        <AfoTableHead 
          headCells={columns} 
          onRequestSort={handleRequestSort} 
          orderBy={orderBy} 
          order={order} 
        />
        <TableBody>
          {stableSort(rows, getComparator(order, orderBy)).map((row, index) => (
            <TableRow key={index} onClick={() => onRowClick(row)}>
              {rowMeta.map((rowDetail) => {
                  const rowName = rowDetail.name;
                  return <TableCell 
                    key={rowName} className='afo-table-cell' 
                    component="th" scope="row" 
                    align={rowName.align ? rowName.align : 'left'}
                    sortDirection={orderBy === rowName ? order : false}
                  >
                      {rowDetail.processor ? rowDetail.processor(row[rowName]) : row[rowName]}
                  </TableCell>
              })}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  ) : null;
}

AfoTable.propTypes = {
  className: PropTypes.string,
  columns: PropTypes.array.isRequired,
  rows: PropTypes.array.isRequired,
  rowMeta: PropTypes.array.isRequired
}

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

const positionEnum = {
  Attacker: 1,
  Midfielder: 2,
  Defender: 3,
  Goalkeeper: 4
}

function positionComparator(a, b, orderBy) {
  if (positionEnum[b[orderBy]] < positionEnum[a[orderBy]]) {
    return -1;
  }
  if (positionEnum[b[orderBy]] > positionEnum[a[orderBy]]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  console.log('Getting Comparator',order, orderBy);
  if (orderBy.toLowerCase() === 'positiontype') {
    return order === 'desc'
      ? (a, b) => positionComparator(a, b, orderBy)
      : (a, b) => -positionComparator(a, b, orderBy);
  }
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function stableSort(array, comparator) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
}
 
export default AfoTable;