import React from 'react';
import { RadialChart } from 'react-vis';

const BroadcastingAnalysis = ({ broadcastingRevenue }) => {
    const myData = [];
    const getFloat = (value, label) => {
        if (value) {
            const flVal = parseFloat(value);
            myData.push({ angle: flVal, label: `${flVal.toFixed(1)}m`, subLabel: `${label}`});
            return flVal;
        } else {
            return 0;
        };
    }

    const equalShare = getFloat(broadcastingRevenue.equalShare, "Equal Share");
    const basicAward = getFloat(broadcastingRevenue.basicAward, "Basic Award");
    const solidarity = getFloat(broadcastingRevenue.solidarity, "Solidarity");
    const featured = getFloat((broadcastingRevenue.minimumFeaturedGames + 1) * broadcastingRevenue.facilityFeePerGame, "Featured Game Facility Fees");
    const overseasPaymentMin = getFloat(broadcastingRevenue.overseasPaymentMin, "Overseas");
    const centralCommercial = getFloat(broadcastingRevenue.centralCommercial, "Central Commercial");
    const totalRevenue = (equalShare + basicAward + solidarity + featured + overseasPaymentMin + centralCommercial).toFixed(1);

    return <>
        <span>Minimum Estimated Broadcasting Revenue: {totalRevenue}m</span>
        {myData.length > 0 || myData[0].value === "0.0" ? 
            <RadialChart
                colorType="linear"
                colorDomain={[0, 2]}
                data={myData}
                width={300}
                height={250} showLabels labelsAboveChildren 
                className={"broadcasting-radial"} />
            : 
            <div>No information found for broadcasting</div>}
    </>;
}

export default BroadcastingAnalysis;