import { useState, useEffect } from "react";
import { standardHeaders } from '../services/common';
 
function useFetch(query, returnObjectName = "", run = true) {
    const [runNow, setRunNow] = useState(run);
    const [thisQuery, setQuery] = useState(query);
    const [rtnObj, setRtnObj] = useState({});
   
    useEffect(() => {
      (async () => {
        if (runNow) {
          try {
            const response = await fetch(process.env.REACT_APP_GRAPH_SERVER, { 
              method: 'POST',
              headers: standardHeaders,
              body: JSON.stringify({ query:thisQuery })
            });
            const json = await response.json();
    
            if (json) {
              if (returnObjectName) {
                setRtnObj((previousState) => ({
                    ...previousState, 
                    ...{ 
                      loading: false,
                      data: json.data[returnObjectName]
                    }
                  }));
              } else {
                setRtnObj((previousState) => ({
                  ...previousState, 
                  ...{
                    loading: false,
                    data: json.data
                  }
                }));
              }
            }
          } catch (error) {
            setRtnObj((previousState) => ({
                ...previousState,
                ...{
                  loading: false,
                  data: null,
                  error: error.message
                }
            }));
          }
          setRunNow(false);
        }
      })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [runNow]);

    return [ rtnObj.data, rtnObj.loading, rtnObj.error, setRunNow, setQuery ];
}
 
export default useFetch;