import React from 'react';
import { useHistory } from 'react-router';
import TeamSelect from './TeamSelect';
import { Loading } from '../../components/loading';
import useFetch from '../../hooks/useFetch';
import { getCompetitionsForCountry } from '../../services/competition';

const TeamSelectContainer = () => {
    const competitionsQuery = getCompetitionsForCountry('England');
    const [ 
        competitionsForCountry, 
        competitionsLoading, 
        competitionLoadError
    ] = useFetch(competitionsQuery, "competitions");
    let history = useHistory();

    function teamSelected(teamId) {
        history.push('/club/' + teamId);
    }

    const loadingOrError = competitionsLoading ? 
        competitionLoadError ? 
            <div>Eek, something went wrong</div> : 
            <Loading>{competitionLoadError}</Loading> 
        : null;
     
    return competitionsForCountry && !competitionsLoading && !competitionLoadError ? 
        <TeamSelect competitions={competitionsForCountry} onTeamSelect={teamSelected} /> : loadingOrError;
}

export default TeamSelectContainer;