export const getCompetitionsForCountry = (country) => {
    return 'query { competitions(filter: { AND: [' + 
            `{ isCurrent: true }, { country: "${country}" },` +
            '{ type: LEAGUE }]})' + 
            '{ name leagueId isCurrent tier logo country start}}';
}

export const getPreviousSeasonsForTier = (tier, country) => {
    return `query { competitions(filter:` +
    `{ isCurrent: false, tier: ${tier}, country: "${country}" }, sort: START_DATE_DESC) { ` +
            `name
            leagueId
            start
            end
    }}`;
}

export const getPreviousLeagueSeasonsForTeam = (teamId) => {
    return `query { ` +
    `leagueCompetitionsForTeam(teamId:${teamId}) { ` +
            `teamId
            competitionId
            leagueId
            year
            start
            end
            competitionName
    }}`;
}