import { getCookie } from '../common';

export default class TeamServices {
    static getTeamsForLeague = (competitionId) => {
        return new Promise((accept, reject) => 
            fetch(process.env.REACT_APP_GRAPH_SERVER,
            {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': 'Bearer ' + getCookie('token')
                },
                body: JSON.stringify({query: `query { teamsInCompetition(competitionId: ${competitionId}) {` + 
                    `teamId
                    standing
                    competitionId
                }}`})
            })
            .then(response => response.json())
            .then(response => {
                console.log(response.data);
                accept(response.data.teamsInCompetition);
            }).catch((err) => {
                reject(err);
            })
        );
    }

    static getTeamFromTeamId = (teamId) => {
        return new Promise((accept, reject) => 
            fetch(process.env.REACT_APP_GRAPH_SERVER,
            {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': 'Bearer ' + getCookie('token')
                },
                body: JSON.stringify({query: getTeamWithTeamId})
            })
            .then(response => response.json())
            .then(response => {
                const team = response.data.getTeam;
                accept(flattenTeamObject(team));
            }).catch((err) => {
                reject(null, err);
            })
        );
    }
}

export const getTeamReturnObjectName = 'getTeam';

export const getTeamWithTeamId = (teamId) => {
    return `query { getTeam(filter: { teamId: ${teamId} }) {` + 
                    `name 
                    logo
                    teamId
                    _id
                    ground {
                        name
                        capacity
                        city
                    }
                }}`;
}

export function flattenTeamObject(team) {
    return {
            name: team.name,
            _id: team._id,
            logo: team.logo,
            ground: team.ground.name,
            capacity: team.ground.capacity,
            city: team.ground.city
        };
}