import { getCookie } from '../common';

export default async function getBroadcastingRevenue (competitionId) {
    try {
        const response = await fetch(process.env.REACT_APP_GRAPH_SERVER,
            {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': 'Bearer ' + getCookie('token')
                },
                body: JSON.stringify({query: `query { broadcastRevenue(filter: { competitionId: ${competitionId} }) {` + 
                    `equalShare
                    basicAward
                    solidarity
                    meritStart
                    centralCommercial
                    overseasPaymentMin
                    minimumFeaturedGames
                    facilityFeePerGame
                }}`})
            });
        
        const jsonResponse = await response.json();
        return jsonResponse.data.broadcastRevenue[0];
    } catch (error) {
        return error;
    }
}