import React from 'react';
import PropTypes from 'prop-types';
import './button.scss';

const Button = ({children, green, onClick, primary, lg ,link, secondary}) => {
    let classes = 'button ';
    classes += primary ? 'primary-btn' : '';
    classes += secondary ? 'secondary-btn' : '';
    classes += green ? 'green-btn' : '';
    classes += lg ? ' lg-btn' : ' md-btn';
    return (<a href={link} className={classes} onClick={onClick}>
        {children}
    </a>);
}

Button.default = {
    link: ''
}

Button.propTypes = {
    green: PropTypes.bool,
    primary: PropTypes.bool
}
 
export default Button;