import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import TeamContext from '../../context/teamContext';
import './HeaderNav.scss';

export default function HeaderNav() {
    const teamInfo = useContext(TeamContext);
    return <header>
        
        <Link to={`/club/${teamInfo.teamId}`}>
            <img src={teamInfo.logo} className="team-logo" alt={`${teamInfo.name} logo`} />
        </Link>
        <nav>
            <ul>
                <li>
                    <Link to={`/club/${teamInfo.teamId}/clubhouse`}>
                        <div className="flex">
                            Clubhouse
                        </div>
                    </Link>
                </li>
                <li>
                    <Link to={`/club/${teamInfo.teamId}/ground`}>
                        Stadium
                    </Link>
                </li>
                <li>
                    <Link to={`/club/${teamInfo.teamId}/squad`}>
                        Squad
                    </Link>
                </li>
            </ul>
        </nav>
    </header>
}