import React, { useContext } from 'react';
import { useHistory } from "react-router-dom";
import TeamContext from '../../../context/teamContext';
import { Loading } from '../../../components/loading';
import { AfoTable } from '../../../components/table';
import useFetch from '../../../hooks/useFetch';
import { getSquadByTeamId } from '../../../services/player';

export default function Squad() {
    const teamInfo = useContext(TeamContext);
    const [ squadList, squadListLoading ] = useFetch(getSquadByTeamId(teamInfo.teamId), 'playersInTeam');
    const history = useHistory();
    const squadColumns = [ { id: "firstName", name: "first" }, 
                           { id: "lastName", name: "last" },
                           { id: "positionType", name: "position" },
                           { id: "dob", name: "age" } 
                        ];
    const rowMeta = [{ name: "firstName"}, { name: "lastName" }, 
        { name: "positionType" }, { name: "dob", processor: ageProcessor }];
    const rowClick = (player) => {
        console.log(player);
        history.push(`squad/${player.playerId}`);
    }

    return teamInfo && !squadListLoading && squadList ? <>
            <h1>{teamInfo.name} squad list</h1>
            <AfoTable className="squad-table" defaultOrderDescBy={"positionType"} 
                columns={squadColumns} onRowClick={rowClick}
                rows={squadList} rowMeta={rowMeta} />
        </> 
    : <Loading />;
}

function ageProcessor(dob) {
    var today = new Date();
    var birthDate = new Date(dob);
    var age = today.getFullYear() - birthDate.getFullYear();
    var m = today.getMonth() - birthDate.getMonth();
    if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
        age--;
    }
    return age;
}