import getBroadcastingRevenue from './getBroadcastingRevenue';

export function getAttendence(competitionId, teamId) {
    return `query { attendances(filter: `+
            `{ competitionId: ${competitionId}, teamId: ${teamId} }) {` + 
                    `attendance
                    seasonTickets
                }}`;
}

export {
    getBroadcastingRevenue
}