/* eslint-disable react-hooks/exhaustive-deps */
import React, { useContext, useEffect, useState } from 'react';
import TeamQueries from '../../services/team/get';
import { Loading } from '../loading';
import useLocalStorage from '../../hooks/useLocalStorage';
import TeamAnalyse from '../teamanalyse';
import './TeamsInLeague.scss';
import TeamCard from './TeamCard';
import { getBroadcastingRevenue } from '../../services/financial';
import CompetitionContext from '../../context/competitionContext';
import { setGameTeam } from '../../services/game';
import useFetch from '../../hooks/useFetch';
import UserContext from '../../context/userContext';

const TeamsInLeague = ({ onTeamSelect }) => {
    const competition = useContext(CompetitionContext);
    const user = useContext(UserContext);
    const competitionId = competition.leagueId;
    const [ teamsByCompetition, setTeamsByCompetition ] = useLocalStorage('teamsByCompetitionId', []);
    const [ teams, setTeams ] = useState([]);
    const [ selectedTeam, setSelectedTeam ] = useState(null);
    const [ broadcasting, setBroadcasting ] = useState(null);
    const [ createGameTeamResult, createGameTeamLoading, error, runCreateGameTeam, updateQuery ]
         = useFetch(
             setGameTeam(competition.leagueId, selectedTeam, user), 
             'createGameTeam', false);

    useEffect(() => {
        (async () => {
            const teamsInCompetition = teamsByCompetition[competitionId];
            if (teamsInCompetition) {
                setTeams(teamsInCompetition);
            } else {
                const teamInCompetitions = await TeamQueries.getTeamsForLeague(competitionId);
                Promise.all(
                    teamInCompetitions.map(async (teamComp) => {
                        const teamDetail = await TeamQueries.getTeamFromTeamId(teamComp.teamId);
                        teamDetail.teamId = teamComp.teamId;
                        return teamDetail;
                    })
                ).then((teams) => {
                    setTeams(teams);
                    setTeamsByCompetition({...teamsByCompetition, [competitionId] : teams });
                });
            }
        })();
    }, []);

    useEffect(() => {
        (async () => {
            if (selectedTeam) {
                const broadcasting = await getBroadcastingRevenue(competitionId);
                setBroadcasting(broadcasting);
            }
        })();
    }, [selectedTeam]);

    useEffect(() => {
        if (createGameTeamResult && !error) {
            onTeamSelect(createGameTeamResult.record.teamId);
        }
    }, [createGameTeamResult])

    function setTeamToUser() {
        updateQuery(setGameTeam(competition.leagueId, selectedTeam, user));
        runCreateGameTeam(true);
    }

    return teams.length > 0 ? 
        selectedTeam && broadcasting ? 
        <>
            <TeamAnalyse 
                onBack={() => setSelectedTeam(null)} 
                broadcastingRevenue={broadcasting} 
                teamInfo={selectedTeam}
                onTeamSelect={setTeamToUser} 
            />
            {createGameTeamLoading && <Loading>Please wait...</Loading>}
            {error && <div>Error selecting team: {error}</div>}
        </>
        :
        <div className="team-cards">
            {teams.map((t) => {
                return <TeamCard 
                    key={t.teamId}
                    onAnalyseTeam={(teamInfo) => setSelectedTeam(teamInfo)}
                    teamInfo={t} 
                />
            })}
        </div>
    : <Loading></Loading>;
}
 
export default TeamsInLeague;