import { standardHeaders } from '../common';

export default async function getCurrentManager(teamId) {
    try {
        const response = await fetch(process.env.REACT_APP_GRAPH_SERVER,
            {
                method: 'POST',
                headers: standardHeaders,
                body: JSON.stringify({
                    query: `query { coaches(filter: { currentTeamId: ${teamId} }) {` + 
                    `firstName
                    lastName
                    knownAs
                    career {
                        teamId,
                        start,
                        end
                    }
                }}`})
            });
        
        const jsonResponse = await response.json();
        return jsonResponse.data.coaches[0];
    } catch (error) {
        return error;
    }
}