import React from 'react';
import PropTypes from 'prop-types';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '../button';

const TeamCard = ({competitionId, onAnalyseTeam, teamInfo}) => {
    const name = teamInfo.name;
    return <Card className="team-card">
        <CardContent>
            <div className="team-content">
                <img src={teamInfo.logo} alt={`${name} logo`}/>
            </div>
        </CardContent>
        <CardActions>
            <Button onClick={() => onAnalyseTeam(teamInfo)} green>Analyse {name}</Button>
        </CardActions>
    </Card>;
}

TeamCard.propTypes = {
    onAnalyseTeam: PropTypes.func,
    teamInfo: PropTypes.object
}
 
export default TeamCard;