import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import Button from '../button';
import { Button as MatButton, Grid, MenuItem, Select } from '@material-ui/core';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { Loading } from '../loading';
import './TeamAnalyse.scss';
import { getPreviousLeagueSeasonsForTeam } from '../../services/competition';
import { getCurrentManager } from '../../services/team';
import { Broadcasting, Matchday } from '../analysis';
import competitionContext from '../../context/competitionContext';
import { formatCompetitionName } from './';
import useFetch from '../../hooks/useFetch';
import { Ground } from '../ground';

const TeamAnalyse = ({ broadcastingRevenue, onBack, onTeamSelect, teamInfo }) => {
    const competition = useContext(competitionContext);
    const [ manager, setManager ] = useState(null);
    const [ season, setSeason ] = useState(null);
    const [ seasons, seasonsLoading ] = 
        useFetch(getPreviousLeagueSeasonsForTeam(teamInfo.teamId), "leagueCompetitionsForTeam")

    useEffect(() => {
        (async () => {
            const manager = await getCurrentManager(teamInfo.teamId);
            setManager(manager);
        })();
    }, [teamInfo.teamId]);

    useEffect(() => {
        (async () => {
            if (!seasonsLoading && seasons && seasons.length) {
                const season = seasons.find((s) => {
                   return s.leagueId === competition.leagueId;
                });
                setSeason(season || seasons[0]);   
            }
        })();
    }, [competition.leagueId, seasons, seasonsLoading]);
    
    const select = !seasonsLoading && season && season.leagueId ?
        <Select value={season.leagueId} onChange={(e) => 
            setSeason(seasons.find(s => s.leagueId === e.target.value))
        } >
            {seasons.map((s) => <MenuItem key={s.leagueId} value={s.leagueId}>
                {formatCompetitionName(s)}
            </MenuItem>)}
        </Select> : null;
        
    return <>
        {manager ? <Grid container>
            <Grid container justify="space-around">
                <Grid item>
                    <MatButton variant="contained" color="secondary" onClick={() => onBack()} startIcon={<ArrowBackIcon />}>Back</MatButton>
                </Grid>
                <Grid item>
                    <Grid container
                        direction="column"
                        justify="center"
                        alignItems="center">
                        <grid item><h3 className="text-center">{teamInfo.name} Financial Analysis</h3></grid>
                        <grid item>{select}</grid>
                    </Grid>
                    <Grid container
                        direction="column"
                        justify="center"
                        alignItems="center">
                        <img src={teamInfo.logo} className="team-logo" alt={`${teamInfo.name} logo`} />
                    </Grid>
                    <Ground capacity={teamInfo.capacity} city={teamInfo.city} name={teamInfo.ground} />
                    {manager !== undefined && <div className="text-center">Current Manager: {manager.knownAs || manager.firstName + ' ' + manager.lastName}</div>}
                </Grid>
                <Grid item>
                    <Broadcasting broadcastingRevenue={broadcastingRevenue} />
                </Grid>
                <Grid item>
                    {season ? <Matchday capacity={teamInfo.capacity} teamId={teamInfo.teamId} season={season} /> : <Loading><div>Loading Matchday Information</div></Loading>}
                </Grid>
            </Grid>
            
            <Grid container
                direction="row"
                justify="space-around"
                alignItems="right">
                <Grid item>
                </Grid>
                <Grid item>
                    <Button lg green onClick={() => onTeamSelect()}>Select this team</Button>
                </Grid>
            </Grid>
        </Grid> : 
        <Loading><div>Finding team information</div></Loading>}
    </>;
}

TeamAnalyse.propTypes = {
    broadcastingRevenue: PropTypes.object.isRequired,
    onTeamSelect: PropTypes.func.isRequired,
    teamInfo: PropTypes.object.isRequired
}
 
export default TeamAnalyse;