import { getCookie } from '../common';

export function getLatestUser() {
    return 'query getLatestUser {' +
        'users(limit: 1, sort: USERID_DESC ) {' +
            'userId' +
        '}' + 
    '}';
}

export default class TeamServices {
    static getUsersFromTwitterId = (twitterId) => {
        return new Promise((accept, reject) => 
            fetch(process.env.REACT_APP_GRAPH_SERVER,
            {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': 'Bearer ' + getCookie('token')
                },
                body: JSON.stringify({query: `query getUsers {
                    users(filter: { twitterId: ${twitterId} }) {
                      screenName
                      name
                      location
                      authLevel
                      userId
                    }
                  }`})
            })
            .then(response => response.json())
            .then(response => {
                accept(response.data.users);
            }).catch((err) => {
                reject(err);
            })
        );
    }

    static getUserInfo = () => {
        if (process.env.NODE_ENV === "production") {
            return new Promise((accept, reject) => {
                fetch(process.env.REACT_APP_GRAPH_SERVER.replace('graphql', 'api/user'),
                {
                    method: 'GET',
                    headers: {
                        'Accept': 'application/json',
                        'Authorization': 'Bearer ' + getCookie('token')
                    }
                })
                .then(response => response.json())
                .then(response => {
                    console.info && console.info(response.user);
                    accept(response.user);
                }).catch((err) => {
                    reject(err);
                })
            });
        } else {
            return new Promise((accept, reject) => {
                accept({
                    userId: 1234
                })
            });
        }
    }

    static createUser = ({ name, screenName, ffoUserId, location, userId }) => {
        return new Promise((accept, reject) => 
            fetch(process.env.REACT_APP_GRAPH_SERVER,
            {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': 'Bearer ' + getCookie('token')
                },
                body: JSON.stringify({query: `mutation addUser { 
                    createUser(record:{ userId: ${ffoUserId}, twitterId: ${userId}, screenName: "${screenName}", name: "${name}", location: "${location}", authLevel: PLAYER}) {
                    recordId,
                    record {
                      name
                      userId
                      screenName
                      location
                      authLevel
                    },
                    error {
                      message
                    }
                  }
                }`})
            })
            .then(response => response.json())
            .then(response => {
                console.log(response.data.createUser);
                accept(response.data.createUser);
            }).catch((err) => {
                reject(err);
            })
        );
    }
}