import dayjs from 'dayjs';

export function getGameTeam(userId) {
    return userId ? `query { gameTeams(filter: `+
            `{ userId: ${userId}}) {` + 
                    `teamId
                    userId
                    startDate
                    endDate
                    currentLeagueId
                }}` : '';
}

export function setGameTeam(currentLeagueId, team, user) {
    const today = new Date();
    const startDate = dayjs(today).format('YYYY-MM-DD');
    return `mutation { createGameTeam(record: {` +
        `teamId: ${team ? team.teamId : ''},` +
        `userId: ${user ? user.userId : ''}, ` +
        `startDate: "${startDate}", ` +
        `currentLeagueId: ${currentLeagueId}` +
    `}) {
        recordId
        record {
            teamId
        }
        error {
            __typename
        }
    }}`;
}
