import React, { useEffect, useState } from 'react'
import { Route, useParams, Switch, useLocation } from 'react-router';
import useFetch from '../../hooks/useFetch';
import { getTeamReturnObjectName, getTeamWithTeamId } from '../../services/team/get';
import TeamContext from '../../context/teamContext';
import Loading from '../../components/loading/Loading';
import HeaderNav from '../../components/navigation/HeaderNav';
import { Clubhouse } from '../../pages/club/clubhouse';
import { Stadium } from '../../pages/club/stadium';
import { Squad } from './squad';
import './Club.scss';
import { Player } from '../player';
import { useContext } from 'react';
import UserContext from '../../context/userContext';

export default function Club() {
    const params = useParams("teamId");
    const location = useLocation();
    const [ teamId, setTeamId ] = useState(null);
    const userInfo = useContext(UserContext);
    const [ teamInfo, teamInfoLoading, teamInfoError, getTeamInfoNow, setQuery ] = 
        useFetch('', getTeamReturnObjectName, false);

    useEffect(() => {
        params.teamId && !isNaN(params.teamId) && setTeamId(params.teamId);
    }, [params.teamId]);

    useEffect(() => {
        if (teamId) {
            setQuery(getTeamWithTeamId(teamId));
            getTeamInfoNow(true);
        }
    }, [getTeamInfoNow, setQuery, teamId]);


    if (teamInfoError) { 
        return <div className="error">teamInfoError</div>;
    }

    return teamInfoLoading ? <Loading />:
    teamInfo ? <>
        <TeamContext.Provider value={teamInfo}>
            <HeaderNav />
            {location.pathname.split('/').length === 3 &&
            <div className="welcome">Hi {userInfo.name}, the owner of {teamInfo.name} FC.</div>}
            <Switch>
                    <Route path={`/club/${teamId}/clubhouse`}>
                        <Clubhouse />
                    </Route>
                    <Route exact path={`/club/${teamId}/ground`}>
                        <Stadium />
                    </Route>
                    <Route exact path={`/club/${teamId}/squad`}>
                        <Squad />
                    </Route>
                    <Route exact path={`/club/${teamId}/squad/:playerId`}>
                        <Player />
                    </Route>
            </Switch>
        </TeamContext.Provider></> : null;
}