import { Grid } from '@material-ui/core';
import React, { useEffect } from 'react'
import { RadialChart } from 'react-vis';
import useFetch from '../../hooks/useFetch';
import { getAttendence } from '../../services/financial';
import { formatCompetitionName } from '../teamanalyse';
import { Loading } from '../loading';

const Matchday = ({ capacity, season, teamId }) => {
    const [ attendanceInfos, attendanceInfosLoading, attError, reRun, setQuery ] = 
        useFetch(getAttendence(season.leagueId, teamId), 'attendances');

    console.log(attendanceInfos, attendanceInfosLoading, attError);
    useEffect(() => {
        setQuery(getAttendence(season.leagueId, teamId));
        reRun(true);
    }, [season.leagueId, reRun, setQuery, teamId]);

    const attendanceInfo = attendanceInfos && attendanceInfos.length ? attendanceInfos[0] : null;
    
    const seasonTickets = attendanceInfo ? attendanceInfo.seasonTickets : 0;
    const matchdayTickets = attendanceInfo ? attendanceInfo.attendance - seasonTickets : 0;
    const unsoldTickets = attendanceInfo ? capacity - attendanceInfo.attendance : 0;

    if (attendanceInfosLoading) {
        return <Loading />;
    }
    return attendanceInfo && !attError ? 
            <div>
                <Grid item xs={8}>
                    <div>Average attendance: {attendanceInfo.attendance.toLocaleString("en-GB")}</div>
                    <div>For {season.name} {new Date(season.start).getFullYear()}/{new Date(season.end).getFullYear()}</div>
                    <RadialChart
                        colorType="linear"
                        colorDomain={[0, 3]}
                        data={[
                            { angle: matchdayTickets, label: `Matchday tickets: ${matchdayTickets.toLocaleString("en-GB")}`} , 
                            { angle: seasonTickets,  label: `Season tickets: ${seasonTickets.toLocaleString("en-GB")}`},
                            { angle: unsoldTickets,  label: `Unsold: ${unsoldTickets.toLocaleString("en-GB")}`}
                        ]}
                        width={300}
                        height={250} showLabels labelsAboveChildren 
                        className={"matchday-radial"} 
                    />  
                </Grid>
            </div> : <div>
                        <span>No data for {formatCompetitionName(season)}</span>
                    </div>;
}


export default Matchday;
